import React, {Component} from 'react';

export default class Projects extends Component {
  render() {
    return (
      <section className="section-wrapper portfolio-section">
          <div className="container-fluid">
              <div className="row">
                  <div className="col-md-12">
                      <div className="section-title" id="project-section">
                          <h1>Academic Projects.</h1>
                      </div>
                  </div>
              </div>
              <div className="row">
                  <div className="col-md-6">
                      <a className="portfolio-item" href="https://github.com/pranay-ashar/AirPassangerForecast" target="_blank" rel="noopener noreferrer">
                          <div className="portfolio-thumb">
                              <img src="img/portfolio-2.jpg" alt="" />
                          </div>

                          <div className="portfolio-info">
                              <h3>Forecasting Demand for Air Passenger Traffic. </h3>
                          </div>
                      </a>
                  </div>
                  <div className="col-md-6">
                      <a className="portfolio-item" href="project-section" target="_blank" rel="noopener noreferrer">
                          <div className="portfolio-thumb">
                              <img src="img/portfolio-1.jpg" alt="" />
                          </div>

                          <div className="portfolio-info">
                              <h3>Predicting values for future US Imports.</h3>
                          </div>
                      </a>
                  </div>
                  <div className="col-md-6">
                      <a className="portfolio-item" href="https://github.com/pranay-ashar/Optimization-of-Menu-Availability-for-Chipotle" target="_blank" rel="noopener noreferrer">
                          <div className="portfolio-thumb">
                              <img src="img/portfolio-3.jpg" alt="" />
                          </div>

                          <div className="portfolio-info">
                              <h3>Optimization of Menu Availability for Chipotle. </h3>
                          </div>
                      </a>
                  </div>
                  <div className="col-md-6">
                      <a className="portfolio-item" href="https://github.com/pranay-ashar/sentiment-analysis-yelp-review" target="_blank" rel="noopener noreferrer">
                          <div className="portfolio-thumb">
                              <img src="img/portfolio-4.jpg" alt="" />
                          </div>

                          <div className="portfolio-info">
                              <h3>Sentiment Analysis on Yelp Review.</h3>
                          </div>
                      </a>
                  </div>

              </div>
          </div>
      </section>
    )
  }
}
