import React, {Component} from 'react';

class Skills extends Component {
  render() {
    return (
      <section className="section-wrapper skills-wrapper gray-bg">
<div className="container-fluid">
<div className="row">
  <div className="col-md-12">
    <div className="section-title">
      <h1>Skills.</h1>
    </div>
  </div>
</div>
{/*.row*/}
<div className="row">
  <div className="col-md-12">
    <div className="progress-wrapper">
      <div className="progress-item">
        <span className="progress-title">Python</span>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={60}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "60%" }}
          >
            <span className="progress-percent"> 62%</span>
          </div>
        </div>
        {/* .progress */}
      </div>
      {/* .skill-progress */}
      <div className="progress-item">
        <span className="progress-title">SQL</span>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={70}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "70%" }}
          >
            <span className="progress-percent"> 70%</span>
          </div>
        </div>
        {/* /.progress */}
      </div>
      {/* /.skill-progress */}
      <div className="progress-item">
        <span className="progress-title">Tableau</span>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={75}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "75%" }}
          >
            <span className="progress-percent"> 75%</span>
          </div>
        </div>
        {/* /.progress */}
      </div>
      {/* /.skill-progress */}
      <div className="progress-item">
        <span className="progress-title">AWS</span>
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={55}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "55%" }}
          >
            <span className="progress-percent"> 55%</span>
          </div>
        </div>
        {/* /.progress */}
      </div>
      {/* /.skill-progress */}
      {/* .skill-progress */}
    </div>
    {/* /.progress-wrapper */}
  </div>
</div>
{/*.row */}
</div>
{/* .container-fluid */}
</section>
)
}
}

export default Skills;
