import React, {Component} from 'react';

class AboutUs extends Component {
  render () {
    return (
      <section className="intro section-wrapper">
          <div className="container-fluid">
              <div className="row">
                  <div className="col-md-12">
                      <div className="section-title">
                          <h1>About Me.</h1>
                      </div>
                  </div>
                  <div className="col-md-12">
                      <p className="intro-pg">
                          A recent graduate who is highly motivated and passionate about helping businesses grow while
                          growing along with it. Well educated with a Master’s and Bachelor’s degree having acquired skills
                          like data analytics, pattern and trend identification and visualization of data insights along the way.
                          Efficient in languages and tools such as Python, R, SQL and Tableau.
                          Multilingual with fluency in English, Hindi and Gujarati.
                      </p>
                  </div>
              </div>
          </div>
      </section>
    )
  }
}

export default AboutUs;
